  --background-color-lime: #c2e297;
  --secondary-background: #e5e5e5;
  --background-color-pink: #ffb8ca;
  --main-gap: 32px;
}

html, body {
  scroll-behavior: smooth;
}

.skroll {
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.skroll-link {
  cursor: pointer;
  display: block;
}

.skroll a {
  width: 30px;
  height: 30px;
  text-align: center;
  background: #ffb8ca;
  border: 2px solid #d41443;
  border-radius: 50%;
  justify-content: center;
  font-size: 30px;
  text-decoration: none;
  display: block;
}

.skroll-up {
  transform: rotate(-90deg);
}

.icon-skroll {
  fill: #fff;
}

.icon-skroll:hover, .icon-skroll:focus {
  fill: #d41443;
}

